/* App.css */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  background: linear-gradient(135deg, #1a1a1a 0%, #0f2027 50%, #203a43 100%);
  color: white;
  min-height: 100vh;
  overflow-x: hidden;
}

.app {
  min-height: 100vh;
  background: linear-gradient(135deg, #111827 0%, #065f46 50%, #064e3b 100%);
}

/* Header */
.header {
  border-bottom: 1px solid rgba(75, 85, 99, 0.3);
  background: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(12px);
  position: relative;
  z-index: 100;
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 64px;
}

.logo {
  display: flex;
  align-items: center;
  gap: 12px;
}

.logo-icon {
  width: 40px;
  height: 40px;
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo h1 {
  font-size: 20px;
  font-weight: 700;
  color: white;
}

.logo-image {
  border-radius: 8px;
  transition: transform 0.2s ease;
}

.logo-image:hover {
  transform: scale(1.05);
}

/* Main Container */
.main-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 32px 24px;
}

/* Stats Grid */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 24px;
  margin-bottom: 32px;
  position: relative;
  z-index: 1;
}

.stat-card {
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(75, 85, 99, 0.3);
  border-radius: 16px;
  padding: 24px;
}

.stat-content {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.stat-label {
  color: #9ca3af;
  font-size: 14px;
  margin-bottom: 8px;
  text-align: center;
}

.stat-value {
  font-size: 24px;
  font-weight: 700;
  color: white;
  text-align: center;
}

.stat-value-green {
  font-size: 24px;
  font-weight: 700;
  color: #10b981;
  text-align: center;
}

/*
.stat-icon {
  width: 48px;
  height: 48px;
  background: rgba(59, 130, 246, 0.2);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
}
*/

/* Content Grid */
.content-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 32px;
}

@media (max-width: 1024px) {
  .content-grid {
    grid-template-columns: 1fr;
  }
}

/* Main Card */
.main-card {
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(75, 85, 99, 0.3);
  border-radius: 16px;
  padding: 24px;
}

.main-card h2 {
  font-size: 20px;
  font-weight: 700;
  color: white;
  margin-bottom: 24px;
}

/* Balance Card */
.balance-card {
  padding: 16px;
  background: rgba(55, 65, 81, 0.5);
  border-radius: 12px;
  margin-bottom: 24px;
}

.balance-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.balance-row:last-child {
  margin-bottom: 0;
}

.balance-row span:first-child {
  color: #9ca3af;
}

.balance-value {
  color: white;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  word-break: break-word;
}

.balance-value-green {
  color: #10b981;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  word-break: break-word;
}

/* Form Section */
.form-section {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.input-group {
  display: flex;
  flex-direction: column;
}

.input-label {
  font-size: 14px;
  font-weight: 500;
  color: #d1d5db;
  margin-bottom: 8px;
}

.input-container {
  position: relative;
}

.amount-input {
  width: 100%;
  background: #374151;
  border: 1px solid #4b5563;
  border-radius: 12px;
  padding: 12px 16px;
  padding-right: 60px;
  color: white;
  font-size: 16px;
  outline: none;
  transition: all 0.2s ease;
}

.amount-input:focus {
  border-color: #10b981;
  box-shadow: 0 0 0 1px #10b981;
}

.amount-input:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.amount-input::placeholder {
  color: #9ca3af;
}

.input-suffix {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  color: #9ca3af;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  font-size: 14px;
}

.conversion-text {
  margin-top: 8px;
  font-size: 14px;
  color: #9ca3af;
}

/* Contribute Button */
.contribute-button {
  width: 100%;
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  border: none;
  border-radius: 12px;
  color: white;
  font-weight: 600;
  padding: 12px 24px;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.contribute-button:hover:not(:disabled) {
  background: linear-gradient(135deg, #059669 0%, #047857 100%);
  transform: translateY(-1px);
}

.contribute-button:disabled {
  background: linear-gradient(135deg, #4b5563 0%, #374151 100%);
  cursor: not-allowed;
  transform: none;
}

.spinner {
  width: 20px;
  height: 20px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Success Card */
.success-card {
  padding: 16px;
  background: rgba(5, 150, 105, 0.2);
  border: 1px solid rgba(5, 150, 105, 0.4);
  border-radius: 12px;
}

.success-text {
  color: #10b981;
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 8px;
}

.success-link {
  color: #6ee7b7;
  text-decoration: none;
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 4px;
  transition: color 0.2s ease;
  word-break: break-word;
}

.success-link:hover {
  color: #a7f3d0;
}

/* Info Panel */
.info-panel {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.info-card {
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(75, 85, 99, 0.3);
  border-radius: 16px;
  padding: 24px;
}

.info-card h3 {
  font-size: 18px;
  font-weight: 700;
  color: white;
  margin-bottom: 16px;
}

/* Steps */
.steps {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.step {
  display: flex;
  align-items: flex-start;
  gap: 12px;
}

.step-number {
  width: 24px;
  height: 24px;
  background: #10b981;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 12px;
  font-weight: 700;
  flex-shrink: 0;
  margin-top: 2px;
}

.step-title {
  color: white;
  font-weight: 500;
  margin-bottom: 4px;
}

.step-desc {
  color: #9ca3af;
  font-size: 14px;
}

/* Strategy Info */
.strategy-info {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.strategy-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.strategy-row span:first-child {
  color: #9ca3af;
  word-break: break-word;
}

.strategy-value-green {
  color: #10b981;
  font-weight: 700;
  word-break: break-word;
}

.strategy-value-yellow {
  color: #f59e0b;
  font-weight: 700;
  word-break: break-word;
}

.strategy-value-orange {
  color: #ea580c;
  font-weight: 700;
  word-break: break-word;
}

.strategy-value-blue {
  color: #3b82f6;
  font-weight: 700;
  word-break: break-word;
}

/* Links */
.links {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.link-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #d1d5db;
  text-decoration: none;
  transition: color 0.2s ease;
  padding: 8px 0;
  word-break: break-word;
}

.link-item:hover {
  color: white;
}

/* Wallet Adapter Overrides */
.wallet-adapter-button {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%) !important;
  border: none !important;
  border-radius: 12px !important;
  font-weight: 600 !important;
  transition: all 0.2s ease !important;
  position: relative !important;
  z-index: 1000 !important;
}

.wallet-adapter-button:hover {
  background: linear-gradient(135deg, #059669 0%, #047857 100%) !important;
  transform: translateY(-1px);
}

.wallet-adapter-dropdown {
  z-index: 1001 !important;
  position: relative !important;
}

.wallet-adapter-dropdown-list {
  z-index: 1002 !important;
  position: absolute !important;
  top: 100% !important;
  right: 0 !important;
  background: #1f2937 !important;
  border: 1px solid #374151 !important;
  border-radius: 12px !important;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.8) !important;
  min-width: 200px !important;
}

.wallet-adapter-dropdown-list-item {
  background: transparent !important;
  border: none !important;
  padding: 12px 16px !important;
  color: white !important;
  cursor: pointer !important;
  transition: background-color 0.2s ease !important;
}

.wallet-adapter-dropdown-list-item:hover {
  background: rgba(55, 65, 81, 0.5) !important;
}

.wallet-adapter-modal-overlay {
  background: rgba(0, 0, 0, 0.8) !important;
  backdrop-filter: blur(4px);
}

.wallet-adapter-modal {
  background: #1f2937 !important;
  border-radius: 16px !important;
  border: 1px solid #374151 !important;
}

.wallet-adapter-modal-title {
  color: white !important;
}

.wallet-adapter-modal-list {
  margin: 0 !important;
}

.wallet-adapter-modal-list-more {
  border-top: 1px solid #374151 !important;
}

.wallet-adapter-modal-list li {
  background: transparent !important;
}

.wallet-adapter-modal-list li:hover {
  background: rgba(55, 65, 81, 0.5) !important;
}

.wallet-adapter-modal-list li button {
  color: white !important;
  border-radius: 8px !important;
}

.wallet-adapter-modal-list li button:hover {
  background: rgba(55, 65, 81, 0.5) !important;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .header-content {
    padding: 0 16px;
  }
  
  .main-container {
    padding: 24px 16px;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }
  
  .content-grid {
    grid-template-columns: 1fr;
    gap: 24px;
  }
  
  .logo h1 {
    font-size: 18px;
  }
  
  .stat-value,
  .stat-value-green {
    font-size: 20px;
  }
  
  .main-card,
  .info-card {
    padding: 20px;
  }
}

@media (max-width: 480px) {
  .header-content {
    padding: 0 8px;
  }
  
  .main-container {
    padding: 20px 12px;
  }
  
  .stat-card,
  .main-card,
  .info-card {
    padding: 16px;
  }
  
  .logo {
    gap: 8px;
    flex: 1;
    min-width: 0;
  }
  
  .logo h1 {
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .logo-icon {
    width: 28px;
    height: 28px;
    flex-shrink: 0;
  }
  
  .logo-image {
    width: 28px !important;
    height: 28px !important;
  }
  
  .header-right {
    gap: 8px;
  }
  
  .mobile-menu-button {
    padding: 6px;
    min-width: 36px;
    min-height: 36px;
  }
}

@media (max-width: 320px) {
  .header-content {
    padding: 0 4px;
  }
  
  .logo h1 {
    font-size: 12px;
  }
  
  .logo-icon {
    width: 24px;
    height: 24px;
  }
  
  .logo-image {
    width: 24px !important;
    height: 24px !important;
  }
  
  .header-right {
    gap: 4px;
  }
  
  .mobile-menu-button {
    padding: 4px;
    min-width: 32px;
    min-height: 32px;
  }
}

/* Additional utility classes */
.text-center {
  text-align: center;
}

.mb-4 {
  margin-bottom: 16px;
}

.mt-4 {
  margin-top: 16px;
}

.hidden {
  display: none;
}

@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Tabs Styles */
.tab-container {
  display: flex;
  gap: 8px;
  margin-bottom: 24px;
}

.tab-button {
  padding: 8px 16px;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(75, 85, 99, 0.3);
  border-radius: 8px;
  color: #d1d5db;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.tab-button:hover {
  opacity: 0.9;
}

.tab-button.active {
  color: white;
}

.tab-fund.active {
  background: rgba(16, 185, 129, 0.2);
  border-color: rgba(16, 185, 129, 0.4);
  color: #10b981;
}

.tab-unstake.active {
  background: rgba(245, 158, 11, 0.2);
  border-color: rgba(245, 158, 11, 0.4);
  color: #f59e0b;
}

.tab-manual.active {
  background: rgba(59, 130, 246, 0.2);
  border-color: rgba(59, 130, 246, 0.4);
  color: #3b82f6;
}

.tab-send-money.active {
  background: rgba(236, 72, 153, 0.2);
  border-color: rgba(236, 72, 153, 0.4);
  color: #ec4899;
}

.tab-gardener.active {
  background: rgba(139, 92, 246, 0.2);
  border-color: rgba(139, 92, 246, 0.4);
  color: #8b5cf6;
}

/* Shrub Send Styles */
.shrub-send-container {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.sub-tab-container {
  display: flex;
  gap: 16px;
  margin-bottom: 24px;
}

.sub-tab-button {
  padding: 8px 16px;
  background: rgba(0, 0, 0, 0.3);
  border: none;
  border-radius: 8px;
  color: white;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: all 0.2s ease;
}

.sub-tab-button.active {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
}

.send-content, .receive-content {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.send-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.code-input-group {
  display: flex;
  gap: 8px;
}

.code-input {
  flex: 1;
  padding: 12px 16px;
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(75, 85, 99, 0.3);
  border-radius: 8px;
  color: white;
  font-size: 16px;
  outline: none;
  text-align: center;
  font-weight: 700;
  letter-spacing: 2px;
}

.code-input.error {
  border-color: rgba(239, 68, 68, 0.5);
}

.generate-button {
  padding: 12px 24px;
  background: rgba(75, 85, 99, 0.3);
  border: 1px solid rgba(75, 85, 99, 0.5);
  border-radius: 8px;
  color: white;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
}

.address-input {
  width: 100%;
  padding: 12px 16px;
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(75, 85, 99, 0.3);
  border-radius: 8px;
  color: white;
  font-size: 16px;
  outline: none;
}

.address-input.error {
  border-color: rgba(239, 68, 68, 0.5);
}

.amount-input.error {
  border-color: rgba(239, 68, 68, 0.5);
}

.send-button {
  padding: 16px;
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.send-button:disabled {
  background: rgba(107, 114, 128, 0.5);
  cursor: not-allowed;
}

.error-message {
  font-size: 12px;
  color: #ef4444;
  margin-top: 4px;
}

.sent-escrows-list, .pending-escrows-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.escrow-item {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(75, 85, 99, 0.3);
  border-radius: 12px;
  padding: 20px;
}

.escrow-info {
  margin-bottom: 16px;
}

.escrow-amount-green {
  font-size: 20px;
  font-weight: 700;
  color: #10b981;
}

.escrow-detail {
  font-size: 14px;
  color: #9ca3af;
  margin-top: 4px;
  word-break: break-word;
}

.recall-button {
  padding: 12px 24px;
  background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  width: 100%;
}

.claim-section {
  margin-top: 16px;
}

.claim-input-group {
  display: flex;
  gap: 8px;
}

.claim-input-wrapper {
  flex: 1;
  position: relative;
}

.claim-code-input {
  width: 100%;
  padding: 12px 16px;
  background: rgba(0, 0, 0, 0.4);
  border: 2px solid rgba(75, 85, 99, 0.3);
  border-radius: 8px;
  color: white;
  font-size: 16px;
  outline: none;
  text-align: center;
  font-weight: 700;
  letter-spacing: 2px;
  transition: border-color 0.2s ease;
}

.claim-code-input.valid-border {
  border-color: rgba(34, 197, 94, 0.5);
}

.claim-code-input.partial-border {
  border-color: rgba(59, 130, 246, 0.5);
}

.claim-code-input.default-border {
  border-color: rgba(75, 85, 99, 0.3);
}

.claim-check-mark {
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  color: #22c55e;
  font-size: 16px;
}

.claim-button {
  padding: 12px 24px;
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
}

.claim-button:disabled {
  opacity: 0.5;
}

.secure-process-notice, .security-warning {
  background: rgba(59, 130, 246, 0.1);
  border: 1px solid rgba(59, 130, 246, 0.3);
  border-radius: 6px;
  padding: 8px;
  margin-top: 8px;
}

.security-warning {
  background: rgba(245, 158, 11, 0.1);
  border-color: rgba(245, 158, 11, 0.3);
}

.notice-title {
  font-size: 12px;
  color: #60a5fa;
  margin: 0;
  font-weight: 500;
}

.security-warning .notice-title {
  color: #f59e0b;
}

.notice-desc {
  font-size: 11px;
  color: #9ca3af;
  margin: 4px 0 0 0;
}

.attempts-exhausted {
  padding: 12px;
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.3);
  border-radius: 8px;
  color: #ef4444;
  font-size: 14px;
  text-align: center;
}

.exhausted-desc {
  font-size: 12px;
  margin-top: 4px;
  color: #9ca3af;
}

.loading {
  text-align: center;
  padding: 20px;
  color: #9ca3af;
}

.no-escrows {
  text-align: center;
  padding: 20px;
  color: #9ca3af;
}

.success-container {
  margin-top: 24px;
  padding: 16px;
  background: rgba(34, 197, 94, 0.1);
  border: 1px solid rgba(34, 197, 94, 0.3);
  border-radius: 8px;
  text-align: center;
}

.success-message {
  color: #22c55e;
  margin-bottom: 8px;
}

.solscan-link {
  color: #3b82f6;
  text-decoration: none;
  font-size: 14px;
  display: inline-flex;
  align-items: center;
  gap: 4px;
  word-break: break-word;
}

/* Additional Shrub Send Styles for enhanced components */
.info-section {
  margin-bottom: 24px;
}

.info-toggle-button {
  width: 100%;
  padding: 12px 16px;
  background: rgba(107, 114, 128, 0.1);
