  padding: 8px;
  background: rgba(34, 197, 94, 0.1);
  border: 1px solid rgba(34, 197, 94, 0.2);
  border-radius: 6px;
}

.breakdown-title {
  font-size: 11px;
  color: #22c55e;
  margin: 0;
  font-weight: 500;
}

.breakdown-details {
  font-size: 11px;
  color: #9ca3af;
  margin-top: 4px;
}

.recipient-receives {
  font-weight: 600;
  color: #22c55e;
}

.code-helper {
  font-size: 12px;
  color: #9ca3af;
  margin-top: 8px;
}

.duplicate-warning {
  font-size: 12px;
  color: #f59e0b;
  margin-top: 8px;
  text-align: center;
}

.fee-notice {
  background: rgba(59, 130, 246, 0.1);
  border: 1px solid rgba(59, 130, 246, 0.3);
  border-radius: 8px;
  padding: 12px;
  margin-top: 16px;
}

.fee-title {
  font-size: 12px;
  color: #60a5fa;
  margin: 0;
  font-weight: 500;
}

.fee-desc {
  font-size: 11px;
  color: #9ca3af;
  margin: 4px 0 0 0;
}

.registry-usage {
  font-size: 12px;
  color: #9ca3af;
  text-align: center;
  margin-top: 8px;
}

.sent-transfers-section {
  margin-top: 32px;
}

.sent-transfers-section h3 {
  margin-bottom: 16px;
}

.no-pending-transfers {
  text-align: center;
  padding: 40px;
  color: #9ca3af;
}

.no-transfers-text {
  margin-top: 16px;
  font-size: 16px;
  font-weight: 500;
}

.address-share-section {
  background: rgba(107, 114, 128, 0.1);
  border: 1px solid rgba(107, 114, 128, 0.2);
  border-radius: 8px;
  padding: 16px;
  margin-top: 16px;
  text-align: center;
}

.share-address-title {
  font-size: 14px;
  margin: 0 0 12px 0;
  font-weight: 500;
  color: #d1d5db;
}

.address-display {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  flex-wrap: wrap;
}

.address-text {
  color: #60a5fa;
  font-family: monospace;
  font-size: 13px;
  word-break: break-word;
}

.copy-address-button {
  background: rgba(59, 130, 246, 0.2);
  border: 1px solid rgba(59, 130, 246, 0.3);
  border-radius: 6px;
  color: #60a5fa;
  font-size: 12px;
  padding: 6px 12px;
  cursor: pointer;
  font-weight: 500;
}

/* Navigation Styles */
.header-right {
  display: flex;
  align-items: center;
  gap: 16px;
  flex-shrink: 0;
  min-width: 0;
}

/* Desktop Navigation */
.desktop-nav {
  display: flex;
  align-items: center;
  gap: 24px;
}

.nav-link {
  color: #d1d5db;
  text-decoration: none;
  font-weight: 500;
  padding: 8px 16px;
  border-radius: 8px;
  transition: all 0.2s ease;
}

.nav-link:hover {
  color: white;
  background: rgba(255, 255, 255, 0.1);
}

/* Desktop Submenu */
.desktop-submenu {
  position: relative;
}

.desktop-submenu-toggle {
  background: none;
  border: none;
  color: #d1d5db;
  font-weight: 500;
  padding: 8px 16px;
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: all 0.2s ease;
}

.desktop-submenu-toggle:hover {
  color: white;
  background: rgba(255, 255, 255, 0.1);
}

.desktop-submenu:hover .desktop-submenu-content {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.desktop-submenu-content {
  position: absolute;
  top: 100%;
  left: 0;
  background: #1f2937;
  border: 1px solid #374151;
  border-radius: 8px;
  padding: 8px 0;
  min-width: 200px;
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  transform: translateY(-10px);
  transition: all 0.2s ease;
  z-index: 1000;
}

.desktop-submenu-link {
  display: block;
  color: #d1d5db;
  text-decoration: none;
  padding: 12px 16px;
  transition: all 0.2s ease;
}

.desktop-submenu-link:hover {
  color: white;
  background: rgba(55, 65, 81, 0.5);
}

/* Mobile Menu Button */
.mobile-menu-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px;
  border-radius: 6px;
  transition: background-color 0.2s ease;
  flex-shrink: 0;
  min-width: 40px;
  min-height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.mobile-menu-button:hover {
  background: rgba(255, 255, 255, 0.1);
}

/* Hamburger Icon */
.hamburger {
  width: 24px;
  height: 18px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.hamburger span {
  display: block;
  height: 2px;
  background: white;
  border-radius: 1px;
  transition: all 0.3s ease;
  transform-origin: center;
}

.hamburger.active span:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
}

.hamburger.active span:nth-child(2) {
  opacity: 0;
}

.hamburger.active span:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
}

/* Mobile Navigation */
.mobile-nav {
  background: rgba(0, 0, 0, 0.95);
  backdrop-filter: blur(12px);
  border-top: 1px solid rgba(75, 85, 99, 0.3);
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease;
}

.mobile-nav.open {
  max-height: 100vh;
}

.mobile-nav-content {
  padding: 16px 24px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.mobile-nav .nav-link {
  color: #d1d5db;
  text-decoration: none;
  font-weight: 500;
  padding: 12px 16px;
  border-radius: 8px;
  transition: all 0.2s ease;
  border-left: 3px solid transparent;
}

.mobile-nav .nav-link:hover {
  color: white;
  background: rgba(16, 185, 129, 0.1);
  border-left-color: #10b981;
}

/* Mobile Submenu */
.mobile-submenu {
  margin-bottom: 8px;
}

.mobile-submenu-toggle {
  width: 100%;
  background: none;
  border: none;
  color: #d1d5db;
  font-weight: 500;
  padding: 12px 16px;
  text-align: left;
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all 0.2s ease;
  border-left: 3px solid transparent;
}

.mobile-submenu-toggle:hover {
  color: white;
  background: rgba(16, 185, 129, 0.1);
  border-left-color: #10b981;
}

.submenu-arrow {
  font-size: 12px;
  transition: transform 0.2s ease;
}

.submenu-arrow.open {
  transform: rotate(180deg);
}

.mobile-submenu-content {
  background: rgba(55, 65, 81, 0.3);
  border-radius: 8px;
  margin-top: 4px;
  padding: 8px 0;
  border-left: 3px solid rgba(16, 185, 129, 0.3);
}

.mobile-submenu-link {
  display: block;
  color: #9ca3af;
  text-decoration: none;
  padding: 10px 24px;
  transition: all 0.2s ease;
}

.mobile-submenu-link:hover {
  color: white;
  background: rgba(16, 185, 129, 0.1);
}

/* Warning Section */
.warning-section {
  background: rgba(239, 68, 68, 0.05);
  border-bottom: 1px solid rgba(239, 68, 68, 0.2);
  backdrop-filter: blur(8px);
}

.warning-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 16px 24px;
}

.warning-box {
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.3);
  border-radius: 8px;
  padding: 16px;
  text-align: center;
}

.warning-title {
  color: #ef4444;
  font-size: 16px;
  font-weight: 600;
  margin: 0 0 8px 0;
}

.warning-text {
  color: #fca5a5;
  font-size: 14px;
  line-height: 1.5;
  margin: 0;
}

/* Responsive warning section */
@media (max-width: 1024px) {
  .warning-container {
    padding: 12px 16px;
  }
  
  .warning-box {
    padding: 12px;
  }
  
  .warning-title {
    font-size: 14px;
  }
  
  .warning-text {
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .warning-container {
    padding: 10px 12px;
  }
  
  .warning-box {
    padding: 10px;
  }
  
  .warning-title {
    font-size: 13px;
  }
  
  .warning-text {
    font-size: 12px;
  }
}